.item {
list-style: none;
display: flex;
position: relative;

padding-top: 5px;
padding-bottom: 5px;
padding-left: 10px;
padding-right: 10px;

border: 1px solid lightgray;
box-shadow: 1px 2px 1px gray;
border-radius: 2px;
}

.item:hover {
    background-color: #eeeeee;
}

.item:not(:last-child) {
    margin-bottom: 10px;
}

.status {
position: absolute;
top: 50%;
margin-top: -10px;
border-radius: 50%;
border: 2px;
background-color: red;
display: inline-block;


width: 20px;
height: 20px;
}

.avatar {
margin-left: 30px;

transition: all .3s ease-in-out;
}

.avatar:hover {
    transform: scale(1.15);
}

.name {
margin-left: 20px;

text-align: center;
font-size: 18px;
font-weight: bold;

}